<template>
  <div class="main_navs">
    <Header></Header>

    <div class="container">
      <div class="profile_title">
        {{$t('prof1')}}
      </div>
      <div class="row">
        <div class="col-xl-3">
          <SidebarMain></SidebarMain>
        </div>
        <div class="col-xl-9">
          <div class="peo_table">
            <table>
              <thead>
              <tr>
                <th>{{meI.full_name}}</th>
              </tr>
              </thead>
              <tbody>
              <tr v-for="(item,i) in results" :key="i">
                <td>
                  <div>
                    <table>
                      <thead>
                      <tr>
                        <th>ID</th>
                        <th>Test nomi</th>
                        <th>Test savoli</th>
                        <th>Tanlangan javob</th>
                      </tr>
                      </thead>
                      <tbody>
                      <tr v-for="ins in item.questionnaire_answers" :key="ins">
                        <td>{{ins.id}}</td>
                        <td>{{ins.test_title}}</td>
                        <td>{{ins.question_title}}</td>
                        <td>{{ins.choices[0].text}}</td>
                      </tr>
                      </tbody>
                    </table>
                  </div>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";
import SidebarMain from "../../../components/SidebarMain";
export default {
  components: {SidebarMain, Footer, Header},
  data(){
    return{
      results:[],
      meI:{}
    }
  },
  mounted() {
    this.$axiosDefault.get(`students/me/`,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            'Accept-Language':'oz'
          }
        }
    )
        .then(res=>{
          this.meI=res.data
          console.log(res)
          this.$axiosDefault.get(`https://api-kasb.maktab.uz/api/v1/admin/questionnaire-answers/?test_id=15`,{
            headers:{
              'Authorization': `Bearer ${localStorage.getItem('reg_token')}`,
            }
          })
              .then(res=>{
                console.log(this.meI.id)
                this.results=res.data.results
                this.results.filter(student => student.id === this.meI.id);

              })

        })
  }
}
</script>
